<template lang="pug">
  .pd20
    <div class="navigate-bar">
      <span>项目审核</span>
    </div>
    el-form.table-search-form(size="medium" :model="searchForm" :inline="true")
      el-form-item(label="审核状态 ：" )
        <el-radio-group v-model="searchForm.approved" @change="reloadTable">
          <el-radio-button label="">全部</el-radio-button>
          <el-radio-button label="0">待审核</el-radio-button>
          <el-radio-button label="1">审核通过</el-radio-button>
          <el-radio-button label="-1">审核失败</el-radio-button>
        </el-radio-group>
      el-form-item
        el-button(type="primary" @click="governmentDialog.show = true") 添加项目上级政府

    gh-table(ref="ghTable" url="/project/getAllProject" :searchForm="searchForm" :showIndex="true")
      <el-table-column prop="name" label="项目名称" />
      <el-table-column prop="creditCode" label="施工许可证号" width="180"/>
      <el-table-column prop="creditCode" label="所属区域" width="180">
        span(slot-scope="scope") {{`${scope.row.constructionProvince}/${scope.row.constructionCounty}/${scope.row.constructionCity}`}}
      </el-table-column>
      <el-table-column prop="account" label="账号" />
      <el-table-column prop="secretKey" label="密钥" />
      <el-table-column prop="ctime" label="发布日期" />
      <el-table-column prop="approved" label="状态">
        <div slot-scope="scope">
          <el-tag v-if="scope.row.approved == 0" type="warning">待审核</el-tag>
          <el-tag v-if="scope.row.approved == 1" type="success">审核通过</el-tag>
          <el-tag v-if="scope.row.approved == -1" type="danger">审核失败</el-tag>
        </div>
      </el-table-column>
      <el-table-column prop="address" label="操作" width="280" fixed="right">
        <div class="table-cell-ctrl" slot-scope="scope">
          <router-link :to="{path:'/front/createProd',query:{uuid:scope.row.uuid}}">详情</router-link>
          // <router-link :to="{path:'/examination',query:{uuid:scope.row.uuid}}">日志</router-link>
          <a v-if="+scope.row.approved !== 1 " @click="examCompany(scope.row.uuid,scope.row.name,1)">通过</a>
          <a v-if="+scope.row.approved !== -1 " @click="examCompany(scope.row.uuid,scope.row.name,-1)">拒绝</a>
          <a @click="delCompany(scope.row.uuid,scope.row.name)">删除</a>
        </div>
      </el-table-column>

    el-dialog(
      :title="dialog.approved == 1 ? '审核通过' : '审核拒绝'" center
      append-to-body :visible.sync="dialog.show" width="500px")
      .dialog-tip
        | 确定{{dialog.approved == 1 ? '通过' : '拒绝'}}
        span.dialog-tip-name {{dialog.projectName}}
        | 的审核？
      el-form(label-position="left" label-width="50px"  :model="dialog.form" ref="form")
        el-form-item(label="账户" v-if="dialog.approved == 1")
          el-input(v-model="dialog.form.account")
        el-form-item(label="密钥" v-if="dialog.approved == 1")
          el-input(v-model="dialog.form.secretKey")
        el-form-item(label="备注")
          el-input(type="textarea" :rows="3" v-model="dialog.form.note")
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog.show = false">取 消</el-button>
        <el-button type="primary" @click="examSubmit">确 定</el-button>
      </span>

    el-dialog(
      title="项目上级政府管理" center class="government-dialog" :before-close="resetDialog"
      append-to-body :visible.sync="governmentDialog.show" width="500px")
      el-form(label-position="left" label-width="80px"  :model="governmentDialog.form" ref="governmentForm")
        el-form-item(label="政府名称:")
          el-input(v-model="governmentDialog.form.name")
             <el-button slot="append" type="primary" @click="addGovernment">添加</el-button>
      div.tag-wrap
        el-tag(type="success" closable @close="delGovernment(item.uuid)" v-for="item in governmentList" :key="item.uuid") {{item.itemValue}}

</template>
<script>
import ghTable from '@/components/table'
export default {
  components: { ghTable },
  data () {
    return {
      searchForm: {
        approved: ''
      },
      governmentList: [],
      governmentDialog: {
        show: false,
        form: {
          name: ''
        }
      },
      dialog: {
        show: false,
        projectUuid: '',
        projectName: 'test',
        approved: 1,
        form: {
          account: '',
          secretKey: '',
          note: ''
        }
      }
    }
  },
  beforeMount () {
    this.getGovernmentList()
  },
  methods: {
    reloadTable () {
      console.log(this.searchForm)
      this.$nextTick(() => {
        this.$refs.ghTable.reloadTableList()
      })
    },
    examCompany (projectUuid, projectName, approved) {
      Object.assign(this.dialog.form, { account: '', secretKey: '', note: '' })
      Object.assign(this.dialog, { projectName, projectUuid, approved, show: true })
    },
    examSubmit () {
      let url = '/project/checkProject'
      let { projectUuid: uuid, approved } = this.dialog
      this.$ajax.post(url, Object.assign({}, this.dialog.form, { uuid, approved }))
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('提交成功')
          this.dialog.show = false
        })
        .catch((e) => {
          this.$message.error(e.msg || '服务器繁忙,提交失败!')
        })
    },
    delCompany (uuid, projectName) {
      this.$confirm(`确认删除${projectName}？`)
        .then((res) => {
          console.log(res)
          this.$ajax.post('/project/deleteProjectByUuid', { uuid })
        })
        .then(() => {
          this.$refs.ghTable.reloadTableList()
          this.$message.success('删除成功')
        })
        .catch((e) => {
          if (e == 'cancel') {
            return
          }
          this.$message.error(e.msg || '服务器繁忙,删除失败!')
        })
    },
    getGovernmentList () {
      (async () => {
        try {
          let res = await this.$ajax.post('/dict/government/get')
          this.governmentList = res.result
        } catch (error) {
          this.$message.error(error.msg || '获取项目上级政府列表失败')
        }
      })()
    },
    addGovernment () {
      (async () => {
        try {
          let res = await this.$ajax.post('/dict/government/add', { itemValue: this.governmentDialog.form.name })
          this.governmentDialog.form.name = ''
          this.getGovernmentList()
        } catch (error) {
          this.$message.error(error.msg || '获取项目上级政府列表失败')
        }
      })()
    },
    delGovernment (uuid) {
      (async () => {
        try {
          let res = await this.$ajax.post('/dict/government/remove', { uuid })
          this.getGovernmentList()
        } catch (error) {
          this.$message.error(error.msg || '获取项目上级政府列表失败')
        }
      })()
    },
    resetDialog (done) {
      this.governmentDialog.form = {}
      done()
    }
  }
}
</script>
<style lang="scss">
@import "~@/styles/_variables";
.dialog-tip {
  margin-bottom: 20px;
  color: #333;
  font-size: 16px;
  &-name {
    color: $orange;
    padding: 0 5px;
  }
}
.tag-wrap {
  width: 100%;
  max-height: 500px;
  overflow: auto;
  .el-tag {
    margin: 0 10px 0 0;
  }
}
.government-dialog {
  .el-input-group__append {
    border-radius: 0 4px 4px 0;
    border: 1px $--color-primary solid;
  }
  .el-input-group__append button.el-button.el-button--primary {
    background: $--color-primary;
    color: #fff;
    border-left: 0;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
  }
}
</style>
